.dashboard {
  &__wrapper {
    @include grid-row($behavior: nest);
    .columns {
      overflow: hidden;
      padding-bottom: rem-calc(40*2/3);

      .heading {
        @include display(flex);
        @include flex-wrap(nowrap);
        @include justify-content(center);
        position: relative;
        margin: 0;
        padding-bottom: rem-calc(25);
        text-align: center;

        &__title {
          @include align-self(flex-end);
          font-size: rem-calc(60*2/3);
          line-height: rem-calc(30);

          &--sub {
            @include align-self(flex-end);
            padding-left: rem-calc(20*2/3);
            font-size: rem-calc(25*2/3);
            line-height: rem-calc(12);
          }
        }
      }
    }
  }

  header.heading {
    text-align: center;

    .heading__title {
      font-size: rem-calc(30*2/3);
    }
  }

  &__calendar,
  &__selection,
  &__grande {
    position: relative;
    background-color: $white;
  }

  &__selection {
    padding-bottom: 0;
  }

  .accordion {
    padding: 0;

    &__link {
      background-color: transparent;
      position: absolute;
      right: 0;
      bottom: rem-calc(25);
      padding: 0;
      margin: 0;

      .display-status {
        bottom: 0;
      }
    }
  }

  &__list {
    margin: rem-calc(25) 0 0;
    list-style: none;

    li {
      @include display(flex);
      @include flex-wrap(nowrap);
      padding: rem-calc(40*2/3) 0;
      border-top: 1px solid $lightGrey;
    }
    &__img {
      padding: 0 rem-calc(30*2/3) 0 rem-calc(20*2/3);
      .inner {
        width: rem-calc(80);
        height: rem-calc(80);
      }
    }
    &__content {
      padding-right: rem-calc(40*2/3);

      p {
        margin: 0;
        font-size: rem-calc(14);

        &:last-child {
          padding-bottom: rem-calc(15*2/3);
        }
      }

      &--title {
        a {
          display: block;
          padding-bottom: rem-calc(8*2/3);
          font-size: rem-calc(14);
          font-family: $font-bold;
          color: $jet;
          &.city {
            color: $blue;
          }
        }
      }
      &--description {
        padding-bottom: rem-calc(15*2/3);
      }
    }
  }

  .btn-primary {
    margin: 0;
  }

  .seemore-link {
    a {
      display: block;
      width: 100%;
      margin: 0;
      border-radius: 0;
    }
  }
}
@media #{$medium-up} {
  .dashboard {
    header.heading {
      .heading__title {
        font-size: rem-calc(30);
      }
    }

    &__calendar,
    &__selection,
    &__grande {
      padding: 0 rem-calc(20);
    }

    .columns {
      .heading {
        display: block;
        padding-top: rem-calc(30);

        &__title {
          padding-bottom: rem-calc(10);

          &--sub {
            padding-bottom: 0;
          }
        }
      }
    }
    &__list {
      margin-top: 0;

      &__img {
        padding-left: 0;
      }
      &__content {
        padding-right: 0;

        &--title {
          a {
            display: inline;
            line-height: 1;

            &.city {
              margin-left: rem-calc(5);
            }
          }
        }

        &--description {
          padding-bottom: rem-calc(12);
        }
      }
    }

    .seemore-link {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
    }
  }
}
